jQuery(document).ready(function($){
    function showFooter () {
        const shift = document.body.clientHeight - window.innerHeight - window.scrollY;
        const footer = document.querySelector("#page-4");
        console.log(`shift = ${shift}`);
        if (shift < 300) {
            footer.style.bottom = `${-shift}px`;
        }
    }
    window.addEventListener('scroll', function (e) {
        let scrolled = window.pageYOffset;
        const page1 = document.querySelector('#page-1');
        page1.style.backgroundPositionY = `${scrolled * 0.5}px`;
    });

});
